.scrollbar {
  position: fixed;
  top: 0;
  right: 0;
  width: .5vw;
  height: 100%;
  background: #000;
  transition: transform var(--animation-time) ease;
  transform: scaleY(calc((var(--scroll-y)) / (var(--document-h) - var(--window-h))));
  transform-origin: top;
}